/* SETTINGS */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Lexend', sans-serif;
    font-weight: 400
}

ul,
li {
    list-style-type: none;
}


/* width */

::-webkit-scrollbar {
    width: 5px;
}


/* Track */

::-webkit-scrollbar-track {
    background: #555;
}


/* Handle */

::-webkit-scrollbar-thumb {
    background: #00FF7F;
}


/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
    background: #111315;
}


/* LOGIN PAGE */

.loginPage {
    height: 100vh;
    width: 100vw;
    overflow-y: hidden;
    background-image: url('images/loginBG.svg');
    background-size: 100%
}

.loginForm {
    max-width: 540px;
    width: 100%;
    padding: 50px;
    border: 1px solid #00FF7F26;
    border-radius: 20px;
    margin: 0 auto;
    text-align: center;
    background: #1E2021;
    position: relative;
}

.loginLogotype {
    width: 176px;
    margin: 0 auto
}

.input {
    width: 100%;
    text-align: left;
}

.input input {
    border: none;
    outline: none;
    background: #111315;
    color: #919191;
    width: 100%;
    height: 54px;
    border-radius: 15px;
    padding-left: 20px;
    padding-right: 20px;
    font-weight: 400;
}

.inputLabel {
    margin-bottom: 10px;
    color: #E3E2E7;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
}

.loginForm .input {
    margin-top: 20px;
}

.input {
    position: relative;
    user-select: none;
}

.input svg {
    position: absolute;
    right: 20px;
    width: 20px;
    height: 20px;
    bottom: 17px;
    cursor: pointer;
    transition: 0.3s
}

.showPassIcon {
    fill: #00FF7F;
}

.loginFormLink {
    color: #00FF7F;
    font-weight: 400;
    font-size: 14px;
    line-height: 15px;
    text-decoration-line: underline;
}

.greenButton {
    padding-top: 12px;
    padding-bottom: 12px;
    background: #00FF7F;
    border: none;
    outline: none;
    color: #111315;
    border-radius: 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    cursor: pointer;
    transition: 0.3s;
}

.greenButton:hover {
    box-shadow: #00ff8032 0px 0px 28px, #00ff8015 0px 0px 10px;
}

.loginPage button {
    width: 100%;
    margin-top: 40px;
}

.loginFormLink {
    text-align: left;
}

.loginForm div {
    text-align: left;
    margin-top: 20px;
}

.container {
    max-width: 1340px;
    width: 100%;
    margin: 0 auto
}


/* DASHBOARD */

.dashboard {
    background: #111315;
    min-height: 100vh;
    padding-bottom: 100px;
}

.logotypeLink img {
    width: 112px;
}

nav {
    position: relative;
    top: 20px;
    display: flex;
    align-items: center;
    margin-bottom: 60px;
    justify-content: space-between;
}

.menuLinks ul {
    display: flex;
    align-items: center;
}

.menuLinks a {
    color: #E3E2E7;
    text-decoration: none;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    transition: 0.3s;
}

.menuLinks a:hover {
    color: #00FF7F;
}

.menuLinks li {
    margin-right: 40px;
}

.profileVisible {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.profileVisible p {
    color: #E3E2E7;
    margin-right: 11px;
}

.profileVisible img {
    margin-right: 11px;
    width: 30px;
    height: 30px;
    border-radius: 100%
}

.navbarProfile {
    display: flex;
    justify-content: flex-end;
}

.profileMenu {
    background: #1E2021;
    border-radius: 10px;
    padding: 20px;
    min-width: 200px;
    padding-top: 10px;
    padding-bottom: 10px;
    position: absolute;
    right: 0;
    top: 50px;
    transition: 0.2s;
    opacity: 0;
    pointer-events: none;
    top: 40px;
    z-index: 999999;
}

.profileMenuOpen {
    opacity: 1;
    top: 50px;
    pointer-events: all;
}

.profileMenu a {
    color: #E3E2E7;
    text-decoration: none;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    transition: 0.3s;
}

.profileMenu a:hover {
    color: #00FF7F;
}

.profileMenu li {
    padding-top: 10px;
    padding-bottom: 10px;
}

.switcher {
    width: 325px;
    background: #1E2021;
    border-radius: 15px;
    padding: 5px;
    display: flex;
    justify-content: space-between;
}

.switch {
    width: 50%;
    text-align: center;
    padding-top: 12px;
    padding-bottom: 12px;
    color: #E3E2E7;
    cursor: pointer;
}

.switchActive {
    background: #00FF7F;
    border-radius: 15px;
    color: #111315
}

.filterButton {
    background: #1E2021;
    border-radius: 10px;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: 0.3s;
    border: 1px solid transparent;
}

.filterButton:hover {
    border: 1px solid #00FF7F;
}

.filter {
    display: flex;
    margin-top: 20px;
}

.filterVisible {
    display: flex;
    align-items: center;
    padding: 15px 20px;
    box-sizing: border-box;
}

.filterWrapper li {
    cursor: pointer;
}

.filterVisible p {
    margin-right: 10px;
    color: #E3E2E7;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 400;
}

.filterList {
    width: 100%;
    padding: 10px;
    background: #1E2021;
    top: 45px;
    border-radius: 12px;
    left: 0;
    position: absolute;
    z-index: 9999;
    display: none;
    border: 1px solid #2D2F30;
    border-radius: 0px 0px 10px 10px;
}

.filterListActive {
    display: block !important;
}

.filterButton {
    position: relative;
}

.filterList li {
    margin-bottom: 10px;
    transition: 0.3s;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #FAFAFA;
    margin-top: 10px;
}

.filterList li:hover {
    color: #00FF7F
}

.filter>div {
    margin-right: 20px;
    margin-bottom: 20px;
}

.filter {
    position: relative;
    left: 0;
    top: 0;
    width: 100vw;
    display: -webkit-inline-box;
    flex-wrap: wrap;
}

.filter::-webkit-scrollbar {
    width: 20px;
    height: 20px;
}

.filterWrapper {
    position: relative !important;
    height: auto;
}

.dashboard {
    overflow-x: hidden;
}

.graph {
    width: 100%;
    background: linear-gradient(177.19deg, rgba(30, 32, 33, 0.5) 2.34%, rgba(30, 32, 33, 0) 97.66%);
    border-radius: 20px;
    color: #E3E2E7;
    border: 2px solid rgba(0, 255, 127, 0.15);
}

.graphHeader {
    display: flex;
    width: 100%;
}

.graphHeader>div {
    width: calc(100% / 9);
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 0px;
    font-size: 12px;
    color: #919191;
    text-align: center;
    position: relative;
    right: 20px;
}

.graphItem>div {
    text-align: center;
    padding-left: 0px;
    position: relative;
    right: 20px;
}

.graphItem {
    padding-top: 20px;
    padding-bottom: 20px;
    stroke-dasharray: 5;
    background-size: 3px 10px;
    display: flex;
    border-top: 2px solid;
    border-image: url('images/line.svg') 200;
}

.graphItem {
    font-weight: 400;
    font-size: 15px;
    line-height: 19px;
    color: #E3E2E7;
}

.graphItem>div {
    width: calc(100% / 9);
}

.tradeType {
    border-radius: 15px;
    padding: 5px 10px;
    font-size: 12px;
}

.coldWallet {
    background: rgba(0, 56, 255, 0.1);
    color: #0066FF;
}

.hotWallet {
    background: rgba(255, 0, 0, 0.1);
    color: #F95151;
}

.tradeStatus {
    background: #00FF7F;
    border-radius: 15px;
    padding: 5px 10px;
    font-size: 12px;
    color: #111315;
}

.graphWallet>div {
    width: calc(100% / 6) !important;
}

.graphItemAvatar {
    display: flex;
    align-items: center;
}

.graphItemAvatar img {
    margin-right: 10px;
}

.chart {
    padding: 30px;
    border: 2px solid rgba(0, 255, 127, 0.15);
    width: 100%;
    min-height: 200px;
    background: linear-gradient(177.19deg, rgba(30, 32, 33, 0.5) 2.34%, rgba(30, 32, 33, 0) 97.66%);
    border-radius: 20px;
    margin-bottom: 30px;
}

.chartHeaderFilter {
    display: flex;
    width: 110px;
    padding-top: 20px;
    padding-bottom: 20px;
    background: #1E2021;
    border-radius: 15px;
    display: flex;
    justify-content: center;
    color: #909090;
    align-items: center;
    cursor: pointer;
}

.chartHeader {
    display: flex;
    justify-content: space-between
}

.chartHeaderFilter svg {
    margin-right: 10px;
}

.chartBody {
    display: flex;
    justify-content: space-between;
}

.chartContainer {
    width: 90%;
}

.chartTime {
    width: 5%;
    justify-content: flex-end;
    display: flex;
    flex-wrap: wrap;
}

.timeFilterButton {
    padding: 6px 15px;
    border: 1px solid rgba(213, 213, 213, 0.2);
    border-radius: 5px;
    transition: 0.3s;
    background: transparent;
    color: #FAFAFA;
    opacity: 0.7;
    cursor: pointer;
    margin-top: 20px;
}

.timeFilterButtonActive {
    background: rgba(0, 255, 127, 0.1);
    border: 1px solid #00FF7F;
    opacity: 1;
}

.tradeBuy {
    background: rgba(0, 255, 127, 0.1);
    color: #00FF7F
}

.tradeSell {
    background: rgba(255, 0, 0, 0.1);
    color: #F95151;
}

.tradeFailed {
    color: #111315;
    background: #F95151;
}

.apexcharts-text {
    font-family: 'Lexend', sans-serif !important;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    color: #ffffff !important;
    opacity: 1 !important;
    position: relative;
    left: 20px
}

.chartHeaderLeft {
    display: flex;
    align-items: center
}

.chartHeaderStats {
    display: flex;
    align-items: center;
}

.chartHeaderStats>div {
    margin-left: 50px;
}

.chartStatsNumber {
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
    color: #FAFAFA;
    margin-bottom: 5px;
}

.chartStatsTitle {
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    text-transform: capitalize;
    color: #919191;
    opacity: 0.7;
}

.greenNumber {
    color: #00FF7F;
}

.redNumber {
    color: #F95151;
}

@media (max-width: 1025px) {
    .loginPage {
        background-size: auto;
    }
}

@media (max-width: 768px) {
    .loginForm {
        max-width: 90%;
    }
}

@media (max-width: 491px) {
    .loginForm {
        padding: 25px;
    }
}

.navbarProfile a {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: rgba(255, 255, 255, 0.35);
    text-decoration: none
}

@media (max-width: 1366px) {
    .container {
        max-width: 90%;
    }
}

@media (max-width: 1024px) {
    .chartHeaderStats>div {
        margin-left: 25px;
    }
}

.filterSearchBlock input {
    width: 100%;
    height: 45px;
    background: #111315;
    border-radius: 15px;
    color: #919191;
    border: none;
    outline: none;
    padding-left: 30px;
}

.filterSearchBlock {
    position: relative;
}

.filterSearchBlock svg {
    position: absolute;
    left: 10px;
    top: 13px;
    width: 15px;
}

.headerFilterMenuButton {
    text-transform: uppercase;
    background: #111315;
    color: white;
    border-radius: 10px;
    padding: 15px 20px;
    cursor: pointer;
    border: none;
    width: 100%;
    transition: 0.3s;
}

.headerFilterMenuButton:hover {
    background: #00FF7F;
    color: #111315;
}

.chartHeaderFilter {
    position: relative;
}

.chartFilterMenu {
    position: absolute;
    right: 0;
    top: 60px;
    background: #1E2021;
    border: 1px solid #2D2F30;
    border-radius: 0px 0px 10px 10px;
    padding: 25px 15px;
    z-index: 999999;
    min-width: 200px;
}

.chartFilterMenu li {
    margin-bottom: 20px;
}

.chartFilterMenu li:last-child {
    margin-bottom: 0;
}

@media (max-width: 1025px) {
    .chartContainer {
        width: 85%;
    }
}

@media (max-width: 769px) {
    .logotypeLink {
        width: 100%;
        text-align: center;
        margin-bottom: 20px;
    }
    nav {
        flex-wrap: wrap
    }
    .chartHeader {
        display: block;
        justify-content: space-between;
    }
    .chartHeaderLeft {
        justify-content: space-between;
        margin-bottom: 20px;
    }
    .chartContainer {
        width: 90%;
    }
    .graphWrapper {
        width: 100%;
        overflow-x: scroll
    }
    .graph {
        width: 170%;
    }
    .chartFilterMenu {
        right: unset;
        left: 0;
    }
    .chartContainer {
        width: 100%;
    }
    .chartBody {
        flex-wrap: wrap;
    }
    .chartTime {
        justify-content: flex-start;
        display: flex;
        width: 100%;
    }
    .timeFilterButton {
        margin-right: 20px;
    }
}

@media (max-width: 491px) {
    .menuLinks ul {
        flex-wrap: wrap;
    }
    .menuLinks li {
        margin-right: 0px;
        margin-bottom: 10px;
    }
    .menuLinks {
        width: 100%;
    }
    .menuLinks ul {
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .navbarProfile {
        width: 100%;
        font-size: 16px;
        padding-right: 10px;
    }
    .menuLinks li {
        padding-left: 10px;
        padding-right: 10px;
    }
    .chartHeaderStats {
        flex-wrap: wrap;
    }
    .chartHeaderLeft {
        display: block;
        align-items: center;
    }
    .chart .filterButton {
        max-width: 130px;
    }
    .chartHeaderStats>div {
        margin-left: 0px;
    }
    .chartHeaderStats {
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .chartHeaderStats>div {
        margin-left: 0px;
        width: 50%;
        margin-top: 20px;
    }
    .graph {
        width: 300%;
    }
}

;